.create-note {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex: 1;
    width: 400px;
    min-width: 400px;
    height: 300px;
    border-radius: 5px;
    background-color: rgba(50, 65, 86, 1);
    padding: 1rem;
    gap: 1rem;
    color: white;
    word-break: break-all;

    &__text-area {
        text-align: left;
        font-size: 1.4rem;
        background-color: transparent;
        border: none;
        outline: none;
        resize: none;
        color: white;
        height: 100%;

        &::-webkit-scrollbar {
            width: 0;
        }
    }

    &__bottom-part {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        &__remaining {
            font-size: 1.2rem;
        }
    }
}